.hero__list--cards {
    display: flex;
    flex-wrap: wrap;
    margin-top: -30px;
    margin-left: -15px;

}

.hero__item {
    text-align: center;
    margin-top: 30px;
    margin-left: 15px;
    flex-basis: calc((100% - 2 * 15px) / 2);
}

.music__thumb {
    margin-bottom: 5px;
}

.music__img-poster {
    border-top-left-radius: 50px;
    border-bottom-right-radius: 50px;
}


.hero__title {
    font-weight: 700;
    line-height: 1.21;
    font-size: 14px;
    margin-bottom: 5px;
    color: var(--title-color);

}

.hero__text {
    line-height: 1.21;
    font-size: 14px;
    margin-bottom: 5px;
    color: var(--main-text-color);
}

.music__link {
    display: flex;
    justify-content: center;
    font-size: 12px;
}

.material-icons {
    font-size: 14px;
    color: var(--main-text-color);
}
